import { getApi } from "../request";
export const fetchGeoByLocation = async (location) => {
    const response = await getApi({
        url: `${process.env.API_BASE}/geocode?lat=${location.lat}&lng=${location.lng}`,
    });
    if (response.features[0]) {
        const locality = response.features[0].properties.context.locality.name;
        const place = response.features[0].properties.context.place.name;
        const region = response.features[0].properties.context.region.name;
        const country = response.features[0].properties.context.country.country_code;
        const postcode = response.features[0].properties.context.postcode.name;
        return {
            locality,
            place,
            region,
            country,
            postcode,
            lat: location.lat,
            lng: location.lng,
        };
    }
    return null;
};
