import { closeOffscreen } from "@/tools/offscreen/closeOffscreen";
import { startOffscreen } from "@/tools/offscreen/startOffscreen";
import { sendMessage } from "@/tools/sendMessage";
import { instanceFactory } from "../factory/instanceFactory";
export class OffscreenService {
    async createClient(node) {
        await startOffscreen("offscreen.html");
        await sendMessage("createClient", node);
    }
    async performLatencyTask(message) {
        const ttl = await sendMessage("performLatencyTask", message);
        instanceFactory.mqttService.publish(`client/latency/${message.clientid}`, JSON.stringify(instanceFactory.messageFactory.generateLatencyResultMessage(message, ttl)));
    }
    async destroyClient() {
        await closeOffscreen();
    }
}
