export class IPLocationStore {
    async saveIP(ip) {
        await chrome.storage.local.set({ ip });
    }
    async getIP() {
        const { ip } = await chrome.storage.local.get("ip");
        return ip;
    }
    async eraseIP() {
        await chrome.storage.local.remove("ip");
    }
    async saveLocation(location) {
        await chrome.storage.local.set({ location });
    }
    async getLocation() {
        const { location } = await chrome.storage.local.get("location");
        return location;
    }
    async eraseLocation() {
        await chrome.storage.local.remove("location");
    }
}
