import { instanceFactory } from "../factory/instanceFactory";
import CryptoJS from "crypto-js";
export class NodeStore {
    async getNodeMap() {
        const { nodeMapEncrypted } = await chrome.storage.local.get("nodeMapEncrypted");
        if (!nodeMapEncrypted) {
            await chrome.storage.local.set({ nodeMapEncrypted: {} });
            return await chrome.storage.local.get("nodeMapEncrypted");
        }
        return nodeMapEncrypted;
    }
    async getNodeByUid(uid) {
        const nodeMap = await this.getNodeMap();
        if (!nodeMap) {
            return undefined;
        }
        return await NodeStore.decryptNode(nodeMap[uid]);
    }
    async putNodeMap(uid, node) {
        const nodeMap = await this.getNodeMap();
        nodeMap[uid] = await NodeStore.encryptNode(node);
        await chrome.storage.local.set({ nodeMapEncrypted: nodeMap });
    }
    async eraseNodeMap() {
        await chrome.storage.local.remove("nodeMapEncrypted");
    }
    static async encryptNode(node) {
        const identity = await instanceFactory.identityService.getIdentity();
        const loginInfo = await instanceFactory.loginInfoStore.getLoginInfo();
        const username = loginInfo?.uid;
        let encryptedPw = "";
        let encryptedIv = "";
        if (username && identity) {
            const key = CryptoJS.SHA256(username + identity);
            const plaintext = node.password;
            const iv = CryptoJS.lib.WordArray.random(16);
            const encrypted = CryptoJS.AES.encrypt(plaintext, key, {
                iv: iv,
                mode: CryptoJS.mode.CBC,
                padding: CryptoJS.pad.Pkcs7,
            });
            encryptedPw = encrypted.toString();
            encryptedIv = iv.toString();
            return {
                clientid: node.clientid,
                username: node.username,
                passwordEncrypted: encryptedPw,
                iv: encryptedIv,
                readSentryNodeDescription: node.readSentryNodeDescription,
            };
        }
        throw new Error(`Failed to encrypt node: username, username=${username}, identity=${identity}`);
    }
    static async decryptNode(encryptedNode) {
        if (encryptedNode === undefined) {
            return undefined;
        }
        const identity = await instanceFactory.identityService.getIdentity();
        const loginInfo = await instanceFactory.loginInfoStore.getLoginInfo();
        const username = loginInfo?.uid;
        if (username && identity) {
            const key = CryptoJS.SHA256(username + identity);
            const ivFromData = CryptoJS.enc.Hex.parse(encryptedNode.iv);
            const decrypted = CryptoJS.AES.decrypt(encryptedNode.passwordEncrypted, key, {
                iv: ivFromData,
                mode: CryptoJS.mode.CBC,
                padding: CryptoJS.pad.Pkcs7,
            });
            return {
                username: encryptedNode.username,
                password: decrypted.toString(CryptoJS.enc.Utf8),
                clientid: encryptedNode.clientid,
                readSentryNodeDescription: encryptedNode.readSentryNodeDescription,
            };
        }
        throw new Error(`Failed to decrypt node: username, username=${username}, identity=${identity}`);
    }
}
