import { instanceFactory } from "@/background/factory/instanceFactory";
const AMPLITUDE_ENDPOINT = process.env.AMPLITUDE_URL
    ? process.env.AMPLITUDE_URL + "/batch"
    : "https://api2.amplitude.com/batch";
const API_KEY = process.env.AMPLITUDE_API_KEY;
async function checkIfAmplitude() {
    const identity = await instanceFactory.identityService.getIdentity();
    if (!identity) {
        return false;
    }
    const hashParts = identity.split("-")[0];
    const num = parseInt(hashParts, 16) % Number(process.env.AMPLITUDE_FACTOR);
    return num == 0;
}
async function report(eventName, firstTimeChecker, firstTimeRecorder, params) {
    if (!(await checkIfAmplitude())) {
        console.log("amplitude disabled");
        return;
    }
    console.log("amplitude enabled");
    const identity = await instanceFactory.identityService.getIdentity();
    const loginInfo = await instanceFactory.loginInfoStore.getLoginInfo();
    const firstTime = !(await firstTimeChecker());
    console.log("report", eventName, firstTime);
    await fetch(AMPLITUDE_ENDPOINT, {
        method: "POST",
        body: JSON.stringify({
            api_key: API_KEY,
            events: [
                {
                    user_id: loginInfo?.uid,
                    device_id: identity,
                    event_type: eventName,
                    event_properties: {
                        first_time: firstTime,
                        ...params,
                    },
                },
            ],
        }),
    });
    if (firstTime) {
        await firstTimeRecorder();
    }
}
export async function reportCopyReferralCode() {
    report("referral_click", instanceFactory.eventStore.checkCopyReferralCode, instanceFactory.eventStore.recordCopyReferralCode);
}
export async function reportLocateClick() {
    report("locate_click", instanceFactory.eventStore.checkLocateClick, instanceFactory.eventStore.recordLocateClick);
}
export async function reportGoDashboardamp() {
    report("dashboard_click", instanceFactory.eventStore.checkGoDashboard, instanceFactory.eventStore.recordGoDashboard);
}
export async function reportPageView(pageViewTime, accountTabViewTime, nodeTabViewTime) {
    console.log("reportPageView", pageViewTime, accountTabViewTime, nodeTabViewTime);
    report("popup_view", instanceFactory.eventStore.checkPageView, instanceFactory.eventStore.recordPageView, {
        page_view_time_msec: pageViewTime,
        reward_tab_view_time_msec: accountTabViewTime,
        status_tab_view_time_msec: nodeTabViewTime,
    });
}
