import { Tabs } from "@/tools/tabs";
import { instanceFactory } from "../factory/instanceFactory";
export class BackgroundService {
    constructor() {
        if ((process.env.RUN_TEST = "true"))
            this.runTest();
        if (process.env.AMPLITUDE_API_KEY) {
            console.log("init amplitude in background service", process.env.AMPLITUDE_API_KEY);
        }
        this.instanceFactory = instanceFactory;
        this.instanceFactory.userSessionService.init();
        setInterval(this.keepAliveTask, this.KEEP_ALIVE_INTERVAL);
        chrome.runtime.onInstalled.addListener(this.onInstallTask);
    }
    instanceFactory = null;
    KEEP_ALIVE_INTERVAL = 1000;
    async keepAliveTask() {
        try {
            await chrome.runtime.sendMessage("ping");
        }
        catch (e) { }
    }
    async onInstallTask() {
        await instanceFactory.identityService.generateIdentity();
        if (process.env.OPEN_DASHBOARD === "true") {
            Tabs.goDashboard();
        }
    }
    async runTest() {
        // run test code here
    }
}
