export class LoginInfoStore {
    async saveLoginInfo(loginInfo) {
        await chrome.storage.local.set({ loginInfo: loginInfo });
    }
    async getLoginInfo() {
        const { loginInfo } = await chrome.storage.local.get("loginInfo");
        return loginInfo;
    }
    async eraseLoginInfo() {
        await chrome.storage.local.remove("loginInfo");
    }
}
