export class MessageFactory {
    generateOnlineMessage(node) {
        return {
            type: "online",
            clientid: node.clientid,
            account: node.username,
            timestamp: Date.now(),
        };
    }
    generateLatencyResultMessage(originalMessage, ttl) {
        return {
            clientid: originalMessage.clientid,
            destcid: originalMessage.destcid,
            taskid: originalMessage.taskid,
            timestamp: Date.now(),
            type: "latency",
            ttl,
        };
    }
}
