var ContextType = chrome.runtime.ContextType;
var Reason = chrome.offscreen.Reason;
let creating;
export async function startOffscreen(path) {
    const offscreenUrl = chrome.runtime.getURL(path);
    const existingContexts = await chrome.runtime.getContexts({
        contextTypes: [ContextType.OFFSCREEN_DOCUMENT],
        documentUrls: [offscreenUrl],
    });
    if (existingContexts.length > 0) {
        return;
    }
    if (creating) {
        await creating;
    }
    else {
        creating = chrome.offscreen.createDocument({
            url: path,
            reasons: [Reason.WEB_RTC],
            justification: "Run WebRTC in background to ping other hosts and help build our edging network.",
        });
        await creating;
        creating = null;
    }
}
