import { v4 as uuidv4 } from "uuid";
import { instanceFactory } from "../factory/instanceFactory";
export class IdentityService {
    async generateIdentity() {
        const currentIdentity = await instanceFactory.identityStore.getIDentity();
        if (!currentIdentity) {
            const uniqueId = uuidv4();
            instanceFactory.identityStore.saveIDentity(uniqueId);
            return uniqueId;
        }
        else {
            return currentIdentity;
        }
    }
    async getIdentity() {
        return await instanceFactory.identityStore.getIDentity();
    }
}
