import { sendMessage } from "./sendMessage";
export class Tabs {
    static logOut() {
        chrome.tabs
            .create({
            url: `${process.env.DASHBOARD_URL}/logout`,
            active: true,
        })
            .then((r) => {
            sendMessage("logout");
        });
    }
    static async goDashboard() {
        sendMessage("reportGoDashboard");
        chrome.tabs
            .create({
            url: `${process.env.DASHBOARD_URL}`,
            active: true,
        })
            .then((r) => { });
    }
    static async learnMore() {
        chrome.tabs
            .create({
            url: `https://gradient.network/`,
            active: true,
        })
            .then((r) => { });
    }
    static async openLink(url) {
        chrome.tabs
            .create({
            url,
            active: true,
        })
            .then((r) => { });
    }
}
