export class EventStore {
    async recordPageView() {
        await chrome.storage.local.set({ pageView: true });
    }
    async checkPageView() {
        const { pageView } = await chrome.storage.local.get("pageView");
        return !!pageView;
    }
    async recordCopyReferralCode() {
        await chrome.storage.local.set({ copyReferralCode: true });
    }
    async checkCopyReferralCode() {
        const { copyReferralCode } = await chrome.storage.local.get("copyReferralCode");
        return !!copyReferralCode;
    }
    async recordLocateClick() {
        await chrome.storage.local.set({ locateClick: true });
    }
    async checkLocateClick() {
        const { locateClick } = await chrome.storage.local.get("locateClick");
        return !!locateClick;
    }
    async recordGoDashboard() {
        await chrome.storage.local.set({ goDashboard: true });
    }
    async checkGoDashboard() {
        const { goDashboard } = await chrome.storage.local.get("goDashboard");
        return !!goDashboard;
    }
}
