import mqttx from "mqtt";
import { instanceFactory } from "../factory/instanceFactory";
class MQTT {
    client = null;
    brokerUrl;
    clientid;
    username;
    password;
    currentReconnectTime = 15000;
    reconnectTimeFloor = 15000;
    reconnectTimeCeiling = Number(process.env.RECONNECT_TIME_CEILING || 600000);
    static instance;
    task = null;
    onConnect = null;
    constructor() { }
    state = "Disconnected";
    reconnectTimeout = null;
    getState() {
        return this.state;
    }
    setState(state) {
        this.state = state;
    }
    static getInstance() {
        if (!MQTT.instance) {
            return null;
        }
        return MQTT.instance;
    }
    static createNewInstance(clientid, username, password, task, onConnect) {
        MQTT.instance = new MQTT();
        MQTT.instance.client = null;
        MQTT.instance.brokerUrl = process.env.MQTT_BROKER_URL;
        MQTT.instance.clientid = clientid;
        MQTT.instance.username = username;
        MQTT.instance.password = password;
        MQTT.instance.task = task;
        MQTT.instance.onConnect = onConnect;
        MQTT.instance.connect(MQTT.instance.task, MQTT.instance.onConnect);
        return MQTT.instance;
    }
    static destroyInstance() {
        if (MQTT.instance) {
            MQTT.instance.disconnect();
        }
    }
    static reconnect() {
        if (MQTT.instance && MQTT.instance.task && MQTT.instance.onConnect) {
            MQTT.instance.disconnect();
            MQTT.instance.connect(MQTT.instance.task, MQTT.instance.onConnect);
        }
    }
    connect(onMessage, onConnect) {
        console.log("on connect func");
        const brokerUrl = this.brokerUrl;
        let options = {
            username: this.username,
            password: this.password,
            clientId: this.clientid,
            reconnectPeriod: 15000,
            protocolVersion: 5,
        };
        this.client = mqttx.connect(brokerUrl, options);
        this.client.on("connect", () => {
            this.setState("Good");
            console.log("connect", this.reconnectTimeCeiling);
            this.currentReconnectTime = this.reconnectTimeFloor;
            if (this.client)
                this.client.options.reconnectPeriod = this.currentReconnectTime;
            instanceFactory.mqttService.resumeSubscribe();
        });
        this.client.on("reconnect", () => {
            console.log("reconnect", "this.currentReconnectTime", this.currentReconnectTime);
            this.currentReconnectTime = Math.min(this.currentReconnectTime * 2, this.reconnectTimeCeiling);
            if (this.client) {
                this.client.options.reconnectPeriod = this.currentReconnectTime;
            }
        });
        this.client.on("close", () => {
            console.log("close");
            this.setState("Disconnected");
        });
        this.client.on("disconnect", (packet) => {
            console.log("disconnect", packet);
            if (packet.reasonCode === 152 && this.reconnectTimeout) {
                clearTimeout(this.reconnectTimeout);
                this.reconnectTimeout = null;
            }
            this.setState("Disconnected");
        });
        this.client.on("offline", () => {
            console.log("offline");
            this.setState("Disconnected");
        });
        this.client.on("error", (error) => {
            console.error("error", error);
            this.setState("Disconnected");
        });
        this.client.on("message", (topic, message) => {
            onMessage(topic, message.toString());
            this.setState("Good");
        });
    }
    subscribe(topic) {
        if (this.client) {
            this.client.subscribe(topic, (err) => {
                if (err) {
                    console.error("Subscription error:", err);
                }
            });
            console.log("subscribe to", topic);
        }
    }
    unsubscribe(topic) {
        if (this.client) {
            this.client.unsubscribe(topic, (err) => {
                if (err) {
                    console.error("Unsubscription error:", err);
                }
                else {
                }
            });
        }
    }
    publish(topic, message, opt) {
        if (this.client) {
            this.client.publish(topic, message, opt, (err) => {
                if (err) {
                    console.error("Publish error:", err);
                }
                else {
                }
            });
        }
        else {
        }
    }
    disconnect() {
        if (this.client) {
            this.client.end(true, () => { });
            this.client = null;
        }
    }
}
export default MQTT;
