// src/config/axiosConfig.ts
import axios from "axios";
import { instanceFactory } from "@/background/factory/instanceFactory";
const getToken = async () => {
    const loginInfo = await instanceFactory.loginInfoStore.getLoginInfo();
    return loginInfo?.token;
};
const logout = async () => {
    await instanceFactory.loginInfoStore.eraseLoginInfo();
};
const refreshToken = async () => {
    console.log("Refreshing token...");
    const loginInfo = await instanceFactory.loginInfoStore.getLoginInfo();
    if (!loginInfo) {
        throw new Error("Login info not found");
    }
    const { refresh, uid } = loginInfo;
    try {
        const newTokenResp = await axios.post(`${process.env.FIREBASE_REFRESH_TOKEN_API_URL}?key=${process.env.FIREBASE_API_KEY}`, `grant_type=refresh_token&refresh_token=${encodeURIComponent(refresh)}`, {
            headers: {
                "Content-Type": "application/x-www-form-urlencoded",
            },
            __doingRefreshToken: true,
        });
        if (newTokenResp.status === 200 && newTokenResp.data["access_token"]) {
            const newToken = newTokenResp.data["access_token"];
            await chrome.storage.local.set({
                loginInfo: {
                    refresh,
                    token: newToken,
                    uid,
                },
            });
            return true;
        }
    }
    catch (error) {
        if (error.response?.status === 401 || error.response?.status === 403) {
            console.log(`Refresh token error [${error.response?.status}], logging out`, error);
            await logout();
            return false;
        }
        console.log("Refresh token error other", error);
        return false;
    }
    return false;
};
axios.interceptors.request.use(async (config) => {
    console.log("axiosConfig request", config);
    const token = await getToken();
    if (token && !config.__doingRefreshToken)
        config.headers.Authorization = `Bearer ${token}`;
    return config;
}, (error) => {
    return Promise.reject(error);
});
axios.interceptors.response.use((response) => response, async (error) => {
    const originalRequest = error.config;
    if (error.response?.status === 403 &&
        !originalRequest.__doingRefreshToken) {
        const refreshSuccess = await refreshToken();
        if (refreshSuccess) {
            originalRequest.headers.Authorization = `Bearer ${await getToken()}`;
            return axios(originalRequest);
        }
        else {
            await logout();
        }
    }
    return Promise.reject(error);
});
export default axios;
